.voorbereiding-container{

    height: 115vh;
    width: 100vw;
    overflow: hidden;
}
.voorbereiding-controller{
    position: absolute;
    left:50vw;
    bottom: 15vh;
    height: 35vh;
    filter: drop-shadow(2px 2px 5px black);
}
.voorbereiding-tijd{
    position: absolute;
    top:35vh;
    right: 5vw;
    width: 350px;
    text-align: center;
    filter: drop-shadow(2px 2px 5px black);
}
.voorbereiding-taktiek{
    position: absolute;
    bottom:0vh;
    left: 5vw;
    width: 350px;
    text-align: center;
    filter: drop-shadow(2px 2px 5px black);
}
.tactiek-container{
    position: relative;
    height: 150vh;
    width: 100vw;
}
.tactiek-logo{
    position: absolute;
    width: 25vh;
    left: 50vw;
    top: 75vh;
    transform: translate(-50%,-50%);
    z-index: 3;
}
.tactiek5{
    position: fixed;
    width: 50vh;
    left: 35vw;
    bottom: 0;
    z-index: 2;
}
.tactiek6{
    position: fixed;
    width: 60vh;
    left: 65vw;
    bottom: 0;
    z-index: 2;
}
.tactiek7{
    position: fixed;
    width: 70vh;
    left: 35vw;
    bottom: 25%;
    z-index: 1;
}
.tactiek9{
    position: fixed;
    width: 80vh;
    left: 60vw;
    bottom: 25%;
    z-index: 1;
}
.tactiek10{
    position: absolute;
    width: 85vh;
    left: 40vw;
    top: -50%;
    z-index: 0;
}